
* {
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

nav {
  width: 100%;
  background: #4A90E2;
}
nav .navBlock {
    width: 60%;
    height: 76px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0 auto;
    position: relative;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
nav .navBlock a {
      display: inline-block;
      text-align: center;
      width: 30%;
      font-size: 24px;
      line-height: 76px;
      color: #00408B;
      text-decoration: none;
      -webkit-transition: all .5s linear;
      transition: all .5s linear;
}
nav .navBlock a:first-child {
      color: white;
      border-bottom: 5px solid #00408B;
}
@media screen and (max-width: 768px) {
nav .navBlock {
        width: 100%;
}
nav .navBlock a {
          font-size: 16px;
}
}
.content {
  width: 100%;
  min-height: calc(100vh - 76px);
  background: #E1E1E1;
  padding: 24px 0;
}
.content .toDoList {
    width: 60%;
    margin: 0 auto 24px auto;
}
.content .toDoList .addToDos {
      width: 100%;
      height: 65px;
      background: #FFF;
      border: 2px solid #C8C8C8;
      border-radius: 5px;
      font-size: 24px;
      color: #C8C8C8;
      padding: 0 33px;
      margin-bottom: 24px;
      line-height: 65px;
      cursor: pointer;
}
.content .toDoList .toDos {
      width: 100%;
      margin-bottom: 8px;
      border-radius: 5px;
      overflow: hidden;
}
.content .toDoList .toDos .briefInformation {
        min-height: 75px;
        background: #F2F2F2;
        padding: 24px 32px;
        position: relative;
}
.content .toDoList .toDos .briefInformation * {
          vertical-align: top;
}
.content .toDoList .toDos .briefInformation input[type="checkbox"] {
          display: none;
}
.content .toDoList .toDos .briefInformation .checkbox + label {
          display: inline-block;
          text-align: center;
          width: 24px;
          height: 24px;
          background: #FFFFFF;
          border-radius: 2px;
          cursor: pointer;
}
.content .toDoList .toDos .briefInformation .checkbox:checked + label {
          background: #4A90E2;
          color: white;
}
.content .toDoList .toDos .briefInformation .checkbox:checked + label::before {
          font-family: FontAwesome;
          content: '\F00C';
}
.content .toDoList .toDos .briefInformation .toDosTitle {
          display: inline-block;
          width: 60%;
          font-size: 24px;
          line-height: 24px;
          margin-left: 16px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          background: transparent;
          border: 0;
          height: 28px;
}
.content .toDoList .toDos .briefInformation ::-webkit-input-placeholder {
          color: black;
}
.content .toDoList .toDos .briefInformation :-ms-input-placeholder {
          color: black;
}
.content .toDoList .toDos .briefInformation ::-ms-input-placeholder {
          color: black;
}
.content .toDoList .toDos .briefInformation ::placeholder {
          color: black;
}
.content .toDoList .toDos .briefInformation .iscomplete {
          text-decoration: line-through;
          color: #9B9B9B;
}
.content .toDoList .toDos .briefInformation .star + label {
          width: 24px;
          height: 24px;
          font-size: 24px;
          cursor: pointer;
          color: white;
          -webkit-text-stroke: 2px black;
          position: absolute;
          top: 24px;
          right: 15%;
}
.content .toDoList .toDos .briefInformation .star:checked + label {
          -webkit-text-stroke: 0;
          color: #F5A623;
}
.content .toDoList .toDos .briefInformation .star + label::before {
          vertical-align: top;
          font-family: FontAwesome;
          content: '\F005';
}
.content .toDoList .toDos .briefInformation .edit {
          font-size: 24px;
          position: absolute;
          top: 24px;
          right: 5%;
          cursor: pointer;
}
.content .toDoList .toDos .briefInformation .edit:hover, .content .toDoList .toDos .briefInformation .isEditIcon {
          color: #4A90E2;
}
.content .toDoList .toDos .briefInformation .otherInformationIcon {
          line-height: 16px;
          margin: 16px 0 -7px 30px;
}
.content .toDoList .toDos .briefInformation .otherInformationIcon i {
            margin-left: 10px;
}
.content .toDoList .toDos .isEditLine {
        border-bottom: 3px solid #C8C8C8;
}
.content .toDoList .toDos .isImportant {
        background: #FFF2DC;
}
.content .toDoList .toDos .otherInformation {
        display: none;
        padding: 24px 0 0 0;
        background: #F2F2F2;
}
.content .toDoList .toDos .otherInformation .deadLine, .content .toDoList .toDos .otherInformation .file, .content .toDoList .toDos .otherInformation .comment {
          width: 80%;
          margin: 0 auto 24px auto;
}
.content .toDoList .toDos .otherInformation .deadLine input {
          width: 160px;
          height: 35px;
          border: 0;
          border-radius: 2px;
          font-size: 16px;
          padding-left: 16px;
}
@media screen and (max-width: 768px) {
.content .toDoList .toDos .otherInformation .deadLine input {
              width: 100%;
              margin-bottom: 5px;
}
}
.content .toDoList .toDos .otherInformation .file .plus {
          font-size: 32px;
          color: #C8C8C8;
          cursor: pointer;
}
.content .toDoList .toDos .otherInformation .file .plus:hover {
          color: #4A90E2;
}
.content .toDoList .toDos .otherInformation .file .uploadFile {
          display: none;
}
.content .toDoList .toDos .otherInformation .comment textarea {
          width: 100%;
          border: 0;
          border-radius: 2px;
          padding: 8px 16px;
}
.content .toDoList .toDos .otherInformation .operating {
          margin: 8px 0 0 20px;
}
.content .toDoList .toDos .otherInformation .cancel, .content .toDoList .toDos .otherInformation .add {
          width: 50%;
          height: 60px;
          border: 0;
          font-size: 24px;
          cursor: pointer;
}
.content .toDoList .toDos .otherInformation .cancel {
          background: white;
          color: red;
}
.content .toDoList .toDos .otherInformation .add {
          color: white;
          background: #4A90E2;
}
.content .toDoList .toDos .isEdit {
        display: block;
}
@media screen and (max-width: 768px) {
.content .toDoList {
        width: 95%;
}
}
